// components/Navbar.jsx
"use client";

import { useState, useRef } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Link from "next/link";
import Image from "next/image";

const navItems = [
  { name: "Home", href: "/" },
  {
    name: "Pages",
    dropdown: [
      { name: "About Us", href: "/about" },
      { name: "Our Team", href: "/team" },
      { name: "Pricing Plans", href: "/pricing" },
      { name: "Contact Us", href: "/contact" },
      { name: "FAQ Page", href: "/faq" },
      { name: "Coming Soon", href: "/coming-soon" },
    ],
  },
  {
    name: "Shop",
    dropdown: [
      // Shop Types
      { name: "Right Sidebar", href: "/shop/right-sidebar" },
      { name: "Left Sidebar", href: "/shop/left-sidebar" },
      { name: "Animated List", href: "/shop/animated-list" },
      { name: "Masonry Grid", href: "/shop/masonry-grid" },
      { name: "Masonry Wide", href: "/shop/masonry-wide" },
      { name: "With Filter", href: "/shop/with-filter" },
      { name: "Shop Boxed", href: "/shop/boxed" },
      { name: "Single Category", href: "/shop/single-category" },

      // Shop Layouts
      { name: "Two Columns Grid", href: "/shop/two-columns" },
      { name: "Three Columns Grid", href: "/shop/three-columns" },
      { name: "Four Columns Grid", href: "/shop/four-columns" },
      { name: "Four Columns Wide", href: "/shop/four-columns-wide" },
      { name: "Five Columns Wide", href: "/shop/five-columns-wide" },
      { name: "Six Columns Wide", href: "/shop/six-columns-wide" },

      // Product Types
      // { name: "Standard", href: "/product/standard" },
      // { name: "External Product", href: "/product/external" },
      // { name: "Grouped Product", href: "/product/grouped" },
      // { name: "Variable Product", href: "/product/variable" },
      // { name: "Virtual Product", href: "/product/virtual" },
      // { name: "Downloadable Product", href: "/product/downloadable" },
      // { name: "Hot Product", href: "/product/hot" },
      // { name: "On Sale Product", href: "/product/sale" },
      // { name: "Out of stock", href: "/product/out-of-stock" },

      // Shop Pages
      { name: "User Dashboard", href: "/account/dashboard" },
      { name: "Order Tracking", href: "/account/order-tracking" },
      { name: "Cart", href: "/cart" },
      { name: "Checkout", href: "/checkout" },
      { name: "My Account", href: "/account" },
    ],
  },

  { name: "Lookbook", href: "/lookbook" },
  {
    name: "Blog",
    dropdown: [
      { name: "All Posts", href: "/blog" },
      { name: "Categories", href: "/categories" },
    ],
  },
  { name: "Elements", href: "/elements" },
];

const Navbar = () => {
  const [openDropdown, setOpenDropdown] = useState(null);
  const timeoutRef = useRef();

  const handleMouseEnter = (name) => {
    clearTimeout(timeoutRef.current);
    setOpenDropdown(name);
  };

  const handleMouseLeave = () => {
    timeoutRef.current = setTimeout(() => setOpenDropdown(null), 200);
  };

  return (
    <nav className="bg-white text-black border-b fixed top-0 left-0 w-full z-50">
      <div
        className="max-w-7xl mx-auto px-2 py-4 flex items-center justify-between"
        style={{ height: "80px" }}
      >
        {/* Logo */}
        <Link href="/">
          <div className="flex items-center space-x-2 cursor-pointer">
            <Image src="/logo.png" alt="Logo" width={40} height={40} />
            <span className="text-xl font-semibold">Lisu</span>
          </div>
        </Link>

        {/* Nav Items */}
        <ul className="hidden md:flex space-x-10 h-20 items-center">
          {navItems.map((item) => (
            <li
              key={item.name}
              className="relative"
              onMouseEnter={() => handleMouseEnter(item.name)}
              onMouseLeave={handleMouseLeave}
            >
              <span className="cursor-pointer hover:text-blue-600 select-none">
                {item.name} {item.dropdown && "▾"}
              </span>

              {/* Invisible buffer zone to avoid flickering */}
              <div className="absolute top-full left-0 w-full h-5"></div>
              <AnimatePresence>
                {openDropdown === item.name && item.dropdown && (
                  <motion.ul
                    initial={{ opacity: 0, y: -20 }}
                    animate={{
                      opacity: 1,
                      y: 0,
                      transition: {
                        duration: 0.2,
                        ease: "easeOut",
                      },
                    }}
                    exit={{
                      opacity: 0,
                      y: -20,
                      transition: {
                        duration: 0.05,
                        ease: "easeIn",
                      },
                    }}
                    className={`${
                      item.dropdown.length > 6
                        ? "fixed left-0 right-0 top-[60px] p-6 overflow-auto max-h-[70vh] grid grid-cols-4 gap-x-6 gap-y-3 bg-white shadow-xl "
                        : "absolute left-0 top-full mt-0 p-2 flex flex-col w-auto bg-white shadow-xl "
                    }`}
                    style={{
                      zIndex: 9999,
                      maxWidth:
                        item.dropdown.length > 6 ? "100vw" : "max-content",
                      minWidth: item.dropdown.length > 6 ? "100%" : "auto",
                      overflowY: "auto",
                    }}
                  >
                    {item.dropdown.map((subItem, index) => (
                      <Link>
                      <li
                        key={subItem.name + index}
                        href={subItem.href}
                        className="relative block px-4 hover:bg-gray-100 py-2  whitespace-nowrap bg-white text-base font-medium duration-300 "
                      >
                        {subItem.name}
                      </li>
                      </Link>
                    ))}
                  </motion.ul>
                )}
              </AnimatePresence>
            </li>
          ))}
        </ul>

        {/* Login button */}
        <button className="text-black hover:text-blue-600 hidden md:block">
          Log in
        </button>

        {/* Mobile menu icon placeholder */}
        <div className="md:hidden text-xl cursor-pointer select-none">☰</div>
      </div>
    </nav>
  );
};

export default Navbar;
